//login.js
import request from '@/utils/requests.js'
import qs from 'qs'
import axios from 'axios'


export function doLogin(username, password) {
  let data = {
    username,
    password
  }
  data = qs.stringify(data)
  return request({
    url: '/login',
    method: 'post',
    data
  })
}
// 注册函数
export function register(username, password) {
  let data = {
    username,
    password
  }
  data = qs.stringify(data)
  return request({
    url: '/register',
    method: 'post',
    data
  })
}
export function fetch(url, data) {
  return new Promise((resolve, reject) => {
    axios.get(url, data)
      .then((res) => {
        alert('api --ok')
        console.log('api --ok')
        resolve(res.data)
      })
      .catch(error => {
        console.log(error);
        reject(error)
      })
  })
}
export default {
  selectData() {
    return fetch('http://localhost:8000/select/line');
  },
  insert_data(data) {
    return fetch('http://localhost:8000/select/tray', data)
  }
}